import { ReactNode } from "react"
import { SIDEBAR_WIDTH } from "@rp/common/settings"
import { useDeviceInfo, useKeyboardShortcuts } from "@rp/react/web/common/hooks"
import { Box, Button, CircularProgress } from "@rp/react/web/mui/core"
import { BOTTOM_NAV_HEIGHT } from "../NavBars"

type Props = {
  width: number | string
  onForward?: () => void
  onBack?: () => void
  disabled?: boolean
  buttonLabel: ReactNode
  activeStep: number
  isLoading: boolean
  withKeyboardShortcuts?: boolean
}

const VerticalScrollStepperFooter = ({
  width,
  onForward,
  onBack,
  disabled,
  buttonLabel,
  activeStep,
  isLoading,
  withKeyboardShortcuts = false,
}: Props) => {
  useKeyboardShortcuts(
    ["Enter"],
    withKeyboardShortcuts
      ? event => {
          !disabled && onForward?.()
          // Prevent propagation to other pages
          event.preventDefault()
        }
      : () => {},
  )

  const { isMobile, safeArea } = useDeviceInfo()
  const safeBottom = Number(safeArea.bottom.replace("px", ""))
  const maxWidth = isMobile ? "calc(100% - 30px)" : `calc(100% - ${SIDEBAR_WIDTH + 30}px)`
  return (
    <Box
      sx={{
        backgroundColor: "surface.background",
        position: "fixed",
        width,
        bottom: isMobile ? BOTTOM_NAV_HEIGHT + safeBottom : 0,
        zIndex: 1,
        paddingBottom: isMobile ? "8px" : "14px",
        maxWidth,
        "& > div": {
          width: "100%",
          borderRadius: 2,
          p: isMobile ? "6px" : 2,
          backgroundColor: "grey.700",
          height: isMobile ? "48px" : "64px",
          display: "flex",
          alignItems: "center",
        },
        "&:after": {
          content: '""',
          position: "absolute",
          pointerEvents: "none",
          top: "-64px",
          height: "64px",
          width: width,
          background: "linear-gradient(0deg, rgba(18, 18, 18, 1) 0%, rgba(18, 18, 18, 0) 100%)",
        },
      }}
    >
      <Box>
        {activeStep > 0 && onBack && (
          <Button variant="text-outlined-mono" size="medium" sx={{}} onClick={onBack}>
            Back
          </Button>
        )}
        <Box sx={{ flexGrow: 1 }}>&nbsp;</Box>
        <Button
          variant="contained"
          data-testid="vertical-scroll-stepper-footer-button"
          sx={{ width: isMobile ? 125 : 140, height: "36px", p: 1 }}
          onClick={onForward}
          disabled={disabled}
        >
          {isLoading ? <CircularProgress size={16} color="inherit" /> : buttonLabel}
        </Button>
      </Box>
    </Box>
  )
}

export { VerticalScrollStepperFooter }
